.tabs-container {
    width: 100%;

    .tabs-buttons {
        width: 100%;
        height: 2.5rem;

        button {
            width: 120px;
            height: 100%;
            background: none;
            border: solid 1px #d4d4d4;
            border-left: none;
            border-bottom: none;
            font-size: 14px;
            outline: none;
            position: relative;
            cursor: pointer;
        }

        button::after {
            content: '';
            position: absolute;
            bottom: 0;
            left: 0;
            width: 100%;
            height: 0px;
            background-color: #007bff;
            transition: height 150ms;
        }

        button:not(.selected):hover::after {
            height: 5px;
        }

        button.selected {
            background-color: #007bff;
            color: #fafafa;
        }
    }
}
